import React, { useState, useEffect, useRef } from "react";
import { Task } from "@/src/types";

interface TodoProps {
  todo: Task;
  todos: Task[];
  setTodos: React.Dispatch<React.SetStateAction<Task[]>>;
}

const Todo = ({ todo, todos, setTodos }: TodoProps) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editedTaskTitle, setEditedTaskTitle] = useState(todo.text);
  const ref = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (isEditing) {
      ref.current?.focus();
    }
  }, [isEditing]);

  const handleEdit = () => {
    setIsEditing(true);
  };

  const handleSave = () => {
    if (!setTodos) {
      console.error("setTodos is not defined");
      return;
    }

    const updatedTodos = todos.map((task) =>
      task.id === todo.id ? { ...task, text: editedTaskTitle } : task
    );

    setTodos(updatedTodos);
    setIsEditing(false);
  };

  return (
    <li className="flex justify-between p-4 bg-white border-l-4 border-blue-500 rounded shadow">
      {isEditing ? (
        <input
          ref={ref}
          type="text"
          value={editedTaskTitle}
          onChange={(e) => setEditedTaskTitle(e.target.value)}
          className="mr-2 py-1 px-2 rounded border-gray-400 border"
        />
      ) : (
        <span>{todo.text}</span>
      )}

      <div>
        {isEditing ? (
          <button className="text-blue-500 mr-3" onClick={handleSave}>
            Save
          </button>
        ) : (
          <button className="text-green-500 mr-3" onClick={handleEdit}>
            Edit
          </button>
        )}
        <button className="text-red-500">Delete</button>
      </div>
    </li>
  );
};

export default Todo;
